@import url(https://fonts.googleapis.com/css?family=Oxygen|Rubik:300);
html,body{width:100%;height:100%;box-sizing:border-box;font-family:"Rubik",Arial,sans-serif;font-size:12px;letter-spacing:.6px}*,*:before,*:after{box-sizing:inherit}body{background:linear-gradient(135deg, pink, #cc1382);display:flex;align-items:center;justify-content:center;flex-direction:column;-webkit-user-select:none;-ms-user-select:none;user-select:none}.hidden{display:none}.container{box-shadow:0 16px 28px 0 rgba(0,0,0,0.8),0 25px 55px 0 rgba(0,0,0,0.21);width:290px;height:420px;background-color:#1F2025;color:#DADADB;border-radius:10px 10px 0px 0px}.header{text-align:center;padding:0 20px 20px 30%;font-size:15.6px;border-bottom:solid #18191d 1px}.header #gradient{width:auto}.start{background:linear-gradient(135deg, #B8D087, #00996D);box-shadow:0 16px 28px 0 rgba(0,0,0,0.8),0 25px 55px 0 rgba(0,0,0,0.21);width:290px;text-align:center;color:white;padding:20px 20px;cursor:pointer;border-radius:0 0 10px 10px}.start:hover{background:#00996D}.start:active{background:linear-gradient(135deg, #B8D087, #00996D)}.reset{background:linear-gradient(135deg, orange, red);box-shadow:0 16px 28px 0 rgba(0,0,0,0.8),0 25px 55px 0 rgba(0,0,0,0.21);width:290px;text-align:center;color:white;padding:20px 20px;cursor:pointer;border-radius:0 0 10px 10px}.reset:hover{background:#ce3c1b}#action-title{position:absolute;font-size:8px;left:50%;margin-top:0;transform:translate(-50%, -50%);background:#1F2025;padding:5px 20px;border-radius:20px;border:solid #636779 1px}.actions{display:flex;align-items:center;text-align:center;color:#636779}.actions div{padding-bottom:0;flex:1 1}.set-timer,.set-break{padding-top:20px;border:solid #18191d 1px}.set-timer{border-right:none;border-left:none}.set-break{border-right:none}.set-display{color:#DADADB;font-size:16.8px;padding-top:10px}.actions .minus-add{font-size:18px;display:flex;padding:0;justify-content:center}.actions .minus-add div{cursor:pointer;margin:1px;padding:5px;margin-bottom:10px;flex:.5 1}.actions .minus-add div:hover{color:#DADADB}.actions .minus-add div:active{color:#353741}.timer{height:200px;display:flex;align-items:center;justify-content:center;flex-direction:row}.timer-display{color:#B8D087;font-size:36px;text-align:center}svg#gradient{width:100px;height:40px;vertical-align:bottom;pointer-events:none}svg#gradient text{fill:url(#linearGradient)}#status{text-align:center;height:20px;margin-top:-30px;margin-bottom:30px;color:#636779}.inner-circle{position:absolute;top:50%;left:50%;transform:translate(-48%, -73.5%)}.outer-circle{position:absolute;top:50%;left:50%;transform:translate(-48%, -73.5%);z-index:1}.progress-color0{fill:url(#greenGradient)}.progress-color1{fill:#353741}.spinner-container{position:absolute;z-index:2;left:50%;top:50%;margin-top:-40px;transform:translate(-50%, -50%)}.spinner-mask{position:absolute;background-color:#1F2025;height:45px;width:50px;margin-top:181px;left:200px;transform:translate(-50%, -50%);z-index:2}.spinner{transform:rotate(150deg);animation:rotator 1.2s linear infinite}@keyframes rotator{0%{transform:rotate(150deg)}20%{transform:rotate(200deg)}100%{transform:rotate(390deg)}}.spinning{stroke-dasharray:187;stroke-dashoffset:185;transform-origin:center;animation:dash 1.2s infinite,colors 1.2s infinite}@keyframes colors{0%{stroke:#B8D087}50%{stroke:#00996D}100%{stroke:#B8D087}}@keyframes dash{0%{stroke-dashoffset:185}50%{stroke-dashoffset:175}100%{stroke-dashoffset:185}}

